import React from 'react'
import { Link } from 'react-scroll'
import {
  NavbarLink, Nav, NavbarContainer,
} from '../Home/styles'


const NavBar = ({ setSect }) => {
  const onLinkClick = sect => {
    setSect(sect)
  }
  return (
    <div>
      <NavbarContainer>
        <Nav>
          <img style={{ width: '100px' }} src="./project-covid-logo.jpg" alt="" />
          <div className="menu">
            <NavbarLink to="/" onClick={() => onLinkClick('home')}>Home</NavbarLink>
            <NavbarLink to="/" onClick={() => onLinkClick('about')}>About</NavbarLink>
            <NavbarLink to="/" onClick={() => onLinkClick('email')}>Sign Up</NavbarLink>
            <NavbarLink to="/" onClick={() => onLinkClick('team')}>Our Team</NavbarLink>
            <NavbarLink to="/faqs">FAQs</NavbarLink>
            <a href="https://twitter.com/projectcovid" rel="noopener noreferrer" target="_blank">
              <img style={{ height: '1rem', padding: '0px 1vw' }} src="./twitter.png" alt="" />
            </a>
          </div>
        </Nav>
      </NavbarContainer>
    </div>
  )
}

export default NavBar
